import useRequest from "~/composables/use-request";

const PasswordAPI = {
  change(payload: IRequestPayloadChangePassword) {
    const { request } = useRequest();

    return request.post<null>("/api/user/change_password", { body: payload });
  },

  reset(payload: { email: string }) {
    const { request } = useRequest();
    return request.post("/api/user/reset_password", { body: payload });
  },

  applyNew(payload: { reset_token: string; new_password: string }) {
    const { request } = useRequest();
    return request.post("/api/user/new_password", { body: payload });
  },
};

export default PasswordAPI;
